* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 1.6;
  color: #333;
  min-height: 100vh;
  background-image: linear-gradient(to right bottom, #f36, #ba265d);
  background-size: cover;
  background-repeat: no-repeat;
}

li {
  list-style-type: none;
}

a {
  display: inline-block;
  text-decoration: none;
}

.logo {
  margin-left: 2rem;
  height: 3.25rem;
}

.search {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex: 0 0 40%;
}
.search__button {
  border: none;
  background-color: #f4f2f2;
  cursor: pointer;
}
.search__button:focus {
  outline: none;
}
.search__button:active {
  transform: translateY(2px);
}
.search__input {
  margin-right: -3.25rem;
  border: none;
  border-radius: 100px;
  padding: 0.7rem 2rem;
  width: 90%;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: #f4f2f2;
  transition: all 0.3s ease;
}
.search__input::placeholder {
  font-weight: 100;
  color: #ccc;
}
.search__input:focus {
  outline: none;
  width: 100%;
  background-color: #f0eeee;
}
.search__icon {
  width: 2rem;
  height: 2rem;
  fill: #999;
}
.search__input:focus + .search__button {
  background-color: #f0eeee;
}

.user-nav {
  display: flex;
  align-items: center;
  align-self: stretch;
}
.user-nav > * {
  padding: 0 2rem;
  height: 100%;
  transition: all 0.3s ease;
  cursor: pointer;
}
.user-nav > *:hover {
  background-color: #f4f2f2;
}
.user-nav__icon-box {
  display: flex;
  position: relative;
  align-items: center;
}
.user-nav__icon {
  width: 2.25rem;
  height: 2.25rem;
  fill: #777;
}
.user-nav__notification {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 1rem;
  top: 1.5rem;
  border-radius: 100px;
  width: 1.75rem;
  height: 1.75rem;
  font-size: 1.2rem;
  color: #fff;
  background-color: #eb2f64;
}
.user-nav__user {
  display: flex;
  align-items: center;
}
.user-nav__user-photo {
  margin-right: 1rem;
  border-radius: 100px;
  height: 3.75rem;
}

.side-nav {
  margin-top: 3.5rem;
  font-size: 1.4rem;
}
.side-nav__item {
  position: relative;
}
.side-nav__item:not(:last-child) {
  margin-bottom: 0.5rem;
}
.side-nav__item::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  width: 3px;
  height: 100%;
  background-color: #eb2f64;
  transform: scaleY(0);
  transition: transform 0.2s, width 0.4s 0.2s cubic-bezier(1, 0, 0, 1), background-color 0.1s;
}
.side-nav__item:hover::before, .side-nav__item--selected::before {
  width: 100%;
  transform: scaleY(1);
}
.side-nav__item:active::before {
  background-color: #f36;
}
.side-nav__link:link, .side-nav__link:visited {
  display: flex;
  position: relative;
  z-index: 10;
  align-items: center;
  padding: 1.5rem 3rem;
  text-transform: uppercase;
  color: #faf9f9;
}
.side-nav__icon {
  margin-right: 2rem;
  width: 1.75rem;
  height: 1.75rem;
  fill: currentColor;
}

.legal {
  padding: 2.5rem;
  font-size: 1.2rem;
  text-align: center;
  color: #ccc;
}

.gallery {
  display: flex;
}
.gallery__photo {
  display: block;
  width: 100%;
}

.overview {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #f4f2f2;
}
.overview__heading {
  padding: 1.5rem 3rem;
  font-weight: 300;
  font-size: 2.25rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.overview__stars {
  display: flex;
  margin-right: auto;
}
.overview__icon-star, .overview__icon-location {
  width: 1.75rem;
  height: 1.75rem;
  fill: #eb2f64;
}
.overview__location {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
}
.overview__icon-location {
  margin-right: 1.5rem;
}
.overview__rating {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-self: stretch;
  margin-left: 3rem;
  padding: 0 2.25rem;
  color: #fff;
  background-color: #eb2f64;
}
.overview__rating-average {
  margin-bottom: -3px;
  font-weight: 300;
  font-size: 2.25rem;
}
.overview__rating-count {
  font-size: 1.2rem;
  text-transform: uppercase;
}

.btn-inline {
  display: inline-block;
  border: none;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  font-size: inherit;
  color: #eb2f64;
  background-color: transparent;
  transition: all 0.3s ease;
  cursor: pointer;
}
.btn-inline:focus {
  outline: none;
  animation: pulsate 1s infinite;
}
.btn-inline:hover {
  color: #333;
}

@keyframes pulsate {
  0% {
    box-shadow: none;
    transform: scale(1);
  }
  50% {
    box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
    transform: scale(1.05);
  }
  100% {
    box-shadow: none;
    transform: scale(1);
  }
}
.paragraph:not(:last-of-type) {
  margin: 2rem;
}

.list {
  display: flex;
  flex-wrap: wrap;
  margin: 3rem 0;
  border-top: 1px solid #f4f2f2;
  border-bottom: 1px solid #f4f2f2;
  padding: 3rem;
}
.list__item {
  flex: 0 0 50%;
  margin-bottom: 0.7rem;
}
.list__item::before {
  content: "";
  display: inline-block;
  mask-image: url("../img/chevron-thin-right.svg");
  mask-size: cover;
  margin-right: 0.7rem;
  width: 1rem;
  height: 1rem;
  background-color: #eb2f64;
}

.recommend {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.3rem;
  color: #999;
}
.recommend__photo {
  border: 3px solid #fff;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  box-sizing: content-box;
}
.recommend__photo:not(:last-child) {
  margin-right: -1.5rem;
}

.container {
  margin: 8rem auto;
  min-height: 50rem;
  max-width: 120rem;
  box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  background-color: #faf9f9;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #f4f2f2;
  height: 7rem;
  font-size: 1.4rem;
  background-color: #fff;
}

.content {
  display: flex;
}

.sidebar {
  display: flex;
  flex: 0 0 18%;
  flex-direction: column;
  justify-content: space-between;
  background-color: #333;
}

.hotel-view {
  flex: 1;
}

.details {
  display: flex;
  border-bottom: 1px solid #f4f2f2;
  padding: 4.5rem;
  background-color: #faf9f9;
}

.description {
  flex: 0 0 60%;
  margin-right: 4.5rem;
  padding: 3rem;
  box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.06);
  font-size: 1.4rem;
  background-color: #fff;
}

.user-reviews {
  flex: 1;
  padding: 3rem;
  background-color: #fff;
}